.Puzzle{
    width: 90vh;
    box-shadow: 0 0 0 1px #fff;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    background-size: 120%;
    background-position: 0% 50%;
    position: relative;
    .attach{
        position:absolute;
        width: 0;
        height: 15vh;
        bottom:100%;
        box-shadow: 0 0 1px 1.5px #fff;
        transform-origin: 100% 100%;
    }
    .attach.a1{
        left: 15vh;
        transform:rotate(-10deg);
    }
    .attach.a2{
        right: 15vh;
        transform:rotate(10deg);
    }
    >span{
        position: relative;
        box-shadow: 0 0 0 1px #fff inset;
        background: rgba(0,0,0,.4) !important;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: #333;
        text-decoration: none;
        display:flex;
        overflow:hidden;
        text-shadow: 0 0 1px #333, 0 0 1px #fff;
        font-weight: 300;
        img{
            box-shadow: 0 0 0 2px #fff inset;
            top:2px;
            left:2px;
            width:calc(100% - 4px);
            height:calc(100% - 4px);
            position: absolute;
            z-index:0;
            opacity:0;
            transition: opacity .3s ease-out;
        }
    }
    >span.blanc{
        position:relative;
        cursor:pointer;
    }
    >span.blanc>span{
        position:relative;  
    }
    >span.blanc::before{
        content:'';
        position:absolute;
        width:77%;
        height:77%;
        border-radius: 20%;
        transform: rotate(45deg);
        background-color: rgba(255,255,255,.7);
        transition: width .1s ease,height .1s ease;  
    }
    >span.blanc:hover::before{
        width:74%;
        height:74%;
    }
    @media only screen and (max-width: 100vh){
        background-size: cover;
        background-position: 50% 50%;
        .attach{
            box-shadow: 0 0 0px 5px #fff;
            bottom:auto;
            top:101%;
            margin-top: 2px;
            transform-origin: 0 0;
            height:12vh;
        }
        .attach.a1{
            left: 12vh;
            transform:rotate(0);
        }
        .attach.a2{
            right: 12vh;
            transform:rotate(0);
        }
    }
    @media only screen and (min-width: 100vh){
        >span.active{
            img{
                opacity:1;
            }
        }
    }
}

.Puzzle.about{
    >span:first-child{
        font-size: 9vh;
    }
    >span{
        width:15vh;
        height:15vh;
    }
    @media only screen and (max-width: 100vh){
        width:48vh;
        max-width: 100vw;
        >span{
            width:12vh;
            max-width: 25vw;
            max-height: 25vw;
            height:12vh;
        }
    }
    @media only screen and (min-width: 100vh){
        >span.mob{
            display: none;
        }
    }
}
.Puzzle.projet{
    >span{
        cursor:pointer;
        width:22.5vh;
        height:22.5vh;
        justify-content: flex-end;
        >span{
            width:100%;
            min-height: 30%;
            background-color: rgba(255,255,255,.5);
            display:flex;
            justify-content: center;
            align-items: center;
            >span{
                margin:5px;
                text-align: center;
            }
        }
    }
    @media only screen and (max-width: 100vh){
        width:46vh;
        >span{
            width:23vh;
            height:23vh;
        }
        >span.mob{
            width:11.5vh;
            height:11.5vh;
            font-size: 70%;
        }
    }
}


@keyframes balance {
    50% {transform:rotate3d(9deg, 9deg9deg);}
}